import { get, requestPost, config } from '@/utils/api.js';
import { getCaptcha, updateUserInfoMobile } from '@/common/getData.js';
import { callPhone} from '@/common/getWxUserInfor.js';
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
const reg1 = /^1[0-9]{10}$/,
	reg2 = /^[1-9][0-9xX]{17}$/,
	reg3 = /^[0-9]{4}$/;
export default {
	components: {
		uniNavBar
	},
	computed: {
		// 当窗口 高度 大于800 是 重新 计算 盒子的上边距
		safeAreaTop() {
			const userSystemInfo = uni.getStorageSync('userSystemInfo');
			const safeAreaTop = userSystemInfo.safeArea.top;
			return safeAreaTop;
		}
	},
	name: 'blacklist',
	data() {
		return {
			staticUrl: config.staticUrl,
			showType: false,
			sec: '',
			phonenum: '',
			captcha: '',
			stop: false,
			callGetPhone: '400-007-2959',
		};
	},
	onLoad(options) {
		console.log(options);
		if (options.bizcode == 12) {
			// 展示 输入 手机号
			this.showType = true;
		} else {
			this.showType = false;
		}
		console.log(this.showType);

		const userData = uni.getStorageSync('userData');
		console.log('调提交接口');
		console.log(userData);
	},
	onHide: function() {
		this.stop = true;
		this.sec = 0;
	},

	onUnload: function() {
		this.stop = true;
		this.sec = 0;
	},
	methods: {
		getyzm() {
			if (this.sec > 0) {
				return false;
			}
			console.log(this.phonenum);
			if (!reg1.test(this.phonenum)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~',
					showCancel: false
				});
			} else {
				getCaptcha(this.phonenum)
					.then(res => {
						if (res.result.businessCode == 0) {
							this.sec = 60;
							this.timer();
						} else {
							uni.showModal({
								title: '提示',
								content: '验证码获取失败！请稍后再试并确认手机号是否输入正确'
							});
						}
					})
					.catch(err => {});
			}
		},
		timer() {
			let timer = null;
			let that = this;
			if (this.stop || this.sec <= 0) {
				this.sec = 0;
				return false;
			}
			this.sec -= 1;
			timer = setTimeout(() => {
				this.timer();
			}, 1000);
		},

		submitForm() {
			if (!reg1.test(this.phonenum)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~',
					showCancel: false
				});
			} else if (!reg3.test(this.captcha)) {
				uni.showModal({
					title: '提示',
					content: '请填写正确的验证码！~',
					showCancel: false
				});
			} else {
				//调提交接口
				const userData = uni.getStorageSync('userData');
				console.log('调提交接口');
				console.log(userData);
				const openid = userData.uinfo.openid;
				updateUserInfoMobile(openid, this.phonenum, this.captcha)
					.then(res => {
						console.log('updateUserInfoMobile')
						console.log(res)
						console.log(res.result.businessCode)
						if (res.result.businessCode == 0) {
							uni.redirectTo({
								url: '../activityEntrance/activityEntrance?q=' + getApp().globalData.qrcode
							});
						} else {
							console.log(2222222222)
							uni.showModal({
								title: '提示',
								content: res.result.msg
							});
						}
					})
					.catch(err => {
						console.log(err);
					});
			}
		},

		// 返回
		back() {
			uni.switchTab({
				url: '../index/index'
			});
		},
		 // 拨打电话给收件人
		callGetPhoneFun() {
			callPhone(this.callGetPhone);
		},
	}
};
